import { mapState, mapGetters } from 'vuex'
export default {
  name: "homepage",
  computed: {
    ...mapState(["accessToken"]),
  },
  data() {
    return {
      totalUser: 0,
      totalPost: 0,
      totalUserBlacklist: 0,
      totalReportPost: 0,
      totalReportUser: 0
    };
  },
  mounted () {
    this.getUsers()
    this.getBlacklistUsers()
    this.getPosts()
    this.getReportPosts()
    this.getReportUsers()
  },
  methods: {
    getUsers() {
      this.totalUser = 0
      this.$axios.$get('/v1/users.information.all?page=1&pageSize=1', {
        headers: {"x-rotor-auth": this.accessToken}
      }).then((response) => {
        this.totalUser = response['_metadata'].totalPages
      }).catch((error) => {})
    },
    getBlacklistUsers() {
      this.totalUser = 0
      this.$axios.$get('/v1/users-blacklists.all?page=1&pageSize=1', {
        headers: {"x-rotor-auth": this.accessToken}
      }).then((response) => {
        this.totalPost = response['_metadata'].totalPages
      }).catch((error) => {})
    },
    getPosts() {
      this.totalUser = 0
      this.$axios.$get('/v1/posts.all?page=1&pageSize=1', {
        headers: {"x-rotor-auth": this.accessToken}
      }).then((response) => {
        this.totalUserBlacklist = response['_metadata'].totalPages
      }).catch((error) => {})
    },
    getReportPosts() {
      this.totalUser = 0
      this.$axios.$get('/v1/report-posts.all?page=1&pageSize=1', {
        headers: {"x-rotor-auth": this.accessToken}
      }).then((response) => {
        this.totalReportPost = response['_metadata'].totalPages
      }).catch((error) => {})
    },
    getReportUsers() {
      this.totalUser = 0
      this.$axios.$get('/v1/report-users.all?page=1&pageSize=1', {
        headers: {"x-rotor-auth": this.accessToken}
      }).then((response) => {
        this.totalReportUser = response['_metadata'].totalPages
      }).catch((error) => {})
    },
    getRandomInt(max) {
      return Math.floor(Math.random() * max);
    },
    viewRoute(link) {
      this.$router.push({
        path: link
      });
    },
    redirectTo(path) {
      this.$router.replace({ path });
    }
  }
};
